// 副標題
.faq_subtitle {
  @include font (32px, 1.97);
  margin-bottom: 36px;
  @include pad {
    margin-bottom: 32px; }
  @include mobile {
    @include font (20px, 3.15);
    margin-bottom: 24px; } }


// 問題集清單
.faq_list {
  margin-bottom: 32px;
  @include pad {
    margin-bottom: 24px; }
  @include mobile {
    margin-bottom: 20px; } }


// 問題標題
.faq_heading {
  @include font (20px, 1.6);
  margin-bottom: 16px;

  @include mobile {
    @include font (16px, 1.5);
    margin-bottom: 8px; } }


// 問題回答
.faq_answer {
  @include font (16px, 1.5);
  margin-left: 95px;

  @include pad {
    margin-left: 59px; }
  @include mobile {
    margin-left: 0;
    font-size: 14px;
    li:last-child {
      margin-top: 21px; } } }
