// 下拉選單
.locate_form {
  margin-bottom: 32px;
  label {
    @include font(24px, 1.25);
    margin-right: 20px;
    white-space: nowrap; }
  select {
    @include font(18px, 1.45);
    width: 324px;
    padding: 8px 19px 11px;
    border-color: $color-gray-200;
    color: $color-gray-600; }

  @include pad {
    margin-bottom: 24px;
    label {
      @include font(16px, 1.875); }
    select {
      @include font(16px, 1.5);
      width: 237px;
      padding: 10px 16px 12px; } }
  @include mobile {
    margin-bottom: 20px;
    label {
      @include font(14px, 2.14);
      margin-right: 12px; }
    select {
      @include font(14px, 1.72);
      width: 100%;
      padding-top: 7px;
      padding-bottom: 9px; } } }


// 門市列表
.location_lists {
  margin: 0 -15px; }

.location_list_item {
  margin-bottom: 32px;
  padding: 0 15px;

  @include pad {
    max-width: 50%;
    margin-bottom: 20px; }
  @include mobile {
    max-width: 100%;
    margin-bottom: 16px; } }

.location_list_img {
  position: relative;
  background: $color-dark;
  overflow: hidden;
  img {
    transition-duration: .3s;
    opacity: 1; }
  .search_icon {
    width: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0; }

  &:hover {
    img {
      transform: scale(1.1);
      transition-duration: .25s;
      opacity: .7; }
    .search_icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.1);
      opacity: .9; } } }


// 地點資訊 - 標題
.location_list_content_title {
  @include font(24px, 1.5);
  padding: 12px 24px;

  @include pad {
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 16px; } }

// 地點資訊 - 內容區塊
.location_list_content {
  padding: 16px 24px;
  li:not(:last-child) {
    margin-bottom: 8px; } }

// 地點資訊 - 內容文字
.location_list_content_text {
  @include font(20px, 1.5);

  @include pad {
    font-size: 14px; } }

.location_list_content_icon {
  @include box-size(24px, 24px);
  margin-right: 8px;
  text-align: center;
  img {
    height: 100%;
    display: block; }

  @include pad {
    @include box-size(20px, 20px); } }

.location_list_btn {
  @include font(19px, 1.42);
  display: block;
  width: 100%;
  padding: 20px;
  text-align: center;
  color: $color-light;
  background: $color-dark;
  transition-duration: .5s;

  &:hover {
    background: $color-gray-600;
    transition-duration: .2s; }

  @include pad {
    @include font(14px, 1.5);
    padding: 16px; } }
