// footer 本體
.footer {
  padding: 32px 0;
  color: $color-light;
  background: $color-primary;

  @include pad {
    padding-bottom: 21px; }
  @include mobile {
    padding-top: 24px;
    padding-bottom: 24px; } }


// footer 內容
.footer_content {
  @include mobile {
    flex-wrap: nowrap;
    flex-direction: row-reverse;
    align-items: start; } }


// footer 連結
.footer_navbar {
  @include mobile {
    display: none; } }

.footer_navbar_item_link {
  @include font(16px, 1.5);
  display: block;
  padding: 12px 24px;
  color: $color-light;

  &:hover {
    color: $color-primary-dark; }

  @include pad {
    padding: 6px 20px; } }

.footer_navbar_item:first-child {
  .footer_navbar_item_link {
    padding-left: 0; } }


// 社群icon
.community_icons_img {
  &:hover {
    img {
      transform: scale(1.1); } }

  @include pad {
    img {
      @include box-size(24px, 24px); } } }

.community_icon:not(:last-child) {
  margin-right: 24px; }


// 聯絡方式
.contact_datas {
  width: 100%;
  padding: 24px 0;

  @include pad {
    padding-top: 12px;
    padding-bottom: 20px; }
  @include mobile {
    padding-top: 0;
    padding-bottom: 24px; } }

.contact_tel, .contact_mail {
  @include font(36px, 1.5);
  span, p {
    display: inline-block; }

  @include pad {
    @include font(20px, 1.5); }
  @include mobile {
    @include font(16px, 1.875); } }

.contact_tel {
  margin-bottom: 16px;

  @include pad {
    margin-bottom: 12px; }
  @include mobile {
    margin-bottom: 6px; } }

.contact_icon {
  @include box-size(48px, 48px);
  margin-right: 16px;

  @include pad {
    @include box-size(24px, 24px);
    margin-right: 8px; }
  @include mobile {
    @include box-size(20px, 20px);
    margin-right: 4px; } }

.contact_icon_img {
  max-width: 100%;
  vertical-align: middle; }

.contact_text {
  color: $color-light;

  &:hover {
    color: $color-primary-dark; } }

// 版權聲明
.statement {
  padding: 32px 0 7px;
  border-top: solid 1px $color-light;

  @include pad {
    padding-top: 24px;
    padding-bottom: 0; }
  @include mobile {
    padding-top: 16px;
    flex-wrap: wrap; } }

.statement_text {
  @include font(16px, 1.5);
  font-weight: 500;

  @include mobile {
    @include font(14px, 1.72);
    width: 100%; } }

.statement_links {
  li {
    display: inline-block;

    @include mobile {
      display: block; } }

  li:first-child {
    margin-right: 48px; }

  @include pad {
    margin-top: 12px; }
  @include mobile {
    margin: 0; } }

.statement_link {
  @include font(14px, 1.5);
  color: $color-light;

  @include mobile {
    padding: 8px 0; }

  &:hover {
    color: $color-primary-dark; } }


.footer_navbar_item_link, .community_icons_img img, .contact_text, .statement_link {
  transition-duration: .25s; }
