// 頁面主區塊
.article_page_content {
  padding-bottom: 160px;

  @include pad {
    margin-bottom: 96px; }
  @include mobile {
    margin-bottom: 56px; } }

// 文章副標
.article_subtitle {
  @include font(28px, 1.5);

  @include pad {
    @include font(16px, 2.625); }
  @include mobile {
    @include font(14px, 1.5); } }

// 文章主區塊
.article_content {
  @include mobile {
    max-width: 100%; } }

// 文章內容區塊
.article_lists {
  margin: 24px 0 9px;

  @include pad {
    margin-bottom: 5px; }
  @include mobile {
    margin-top: 20px;
    margin-bottom: -3px; } }

// 文章內容標題
.article_title {
  @include font(48px, 1.98);

  @include pad {
    @include font(32px, 2.97); }
  @include mobile {
    @include font(20px, 1.5); } }

// 文章內容文字
.article_text {
  @include font(20px, 1.5);
  padding: 24px 0 40px;
  color: $color-gray-700;

  li:first-child {
    margin-bottom: 30px; }

  @include pad {
    font-size: 16px;
    padding-top: 16px;
    padding-bottom: 24px;

    li:first-child {
      margin-bottom: 24px; } } }


// 下方資訊
.other_msg {
  .blog_tags {
    display: none; }

  @include pad {
    max-width: 100%; }
  @include mobile {
    .blog_tags {
      display: block;
      margin-top: 30px; } } }


// 分享 icons
.share_icons {
  @include font(20px, 1.5);
  margin-bottom: 20px;
  p {
    margin-right: 20px; }

  @include pad {
    @include font(16px, 1.875);
    p {}
    margin-right: 16px; }
  @include mobile {
    margin-bottom: 15px; } }

.share_icon {
  display: block;
  width: 20px;
  margin-right: 16px;
  transition-duration: .25s;

  &:hover {
    transform: scale(1.2);
    transition-duration: .25s; } }

.share_icon:last-child {
  transform: scale(1.2);

  &:hover {
    transform: scale(1.4); } }


// 上下頁切換
.blog_page_switch {
  padding-top: 24px;
  border-top: solid 1px $color-gray-300;

  p:nth-child(2) {
    display: none;
    @include mobile {
      display: block; } }

  @include pad {
    padding-top: 16px; }
  @include mobile {
    flex-direction: column;
    align-items: start;
    padding-top: 10px; } }

.blog_page_switch_start, .blog_page_switch_end {
  @include font(19px, 1.579);
  position: relative;
  display: block;
  color: $color-dark;
  transition-duration: .2s;

  &:hover {
    color: $color-primary;
    transition-duration: .2s; }

  @include pad {
    @include font(16px, 1.875); }
  @include mobile {
    @include font(14px, 2.143); } }


.blog_page_switch_start {
  &::before {
    content: '';
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    bottom: -5px;
    margin-right: 8px;
    background-image: url('../images/icons/icon-arrow-left.svg');
    transition-duration: .2s; }

  &::after {
    content: '';
    position: absolute;
    width: 0;
    right: 0;
    bottom: 0;
    border: solid 1px transparent;
    transition-duration: .2s; }

  &:hover {
    &::before {
      background-image: url('../images/icons/icon-arrow-left-red.svg');
      transform: translateX(-3px);
      transition-duration: .2s; }
    &::after {
      left: 0;
      width: 100%;
      border-color: $color-primary;
      transition-duration: .2s; } }

  @include pad {
    &::before {
      margin-right: 4px; } }
  @include mobile {
    margin-bottom: 8px;
    &::before {
      margin-right: 0; } } }

.blog_page_switch_end {
  &::before {
    content: '';
    position: absolute;
    width: 0;
    right: 0;
    bottom: 0;
    border: solid 1px transparent;
    transition-duration: .2s; }
  &::after {
    content: '';
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    bottom: -5px;
    margin-left: 8px;
    background-image: url('../images/icons/icon-arrow.svg');
    transition-duration: .2s; }

  &:hover {
    &::before {
      left: 0;
      width: 100%;
      border-color: $color-primary;
      transition-duration: .2s; }
    &::after {
      background-image: url('../images/icons/icon-arrow-right-red.svg');
      transform: translateX(3px);
      transition-duration: .2s; } }

  @include pad {
    &::after {
      margin-left: 4px; } }
  @include mobile {
    display: none; } }


// 文章標籤( 全部落格 )
.blog_tags {
  display: block;
  padding-left: 30px;

  @include mobile {
    display: none; } }

.blog_tags_title {
  @include font(28px, 1.5);
  margin-bottom: 9px;

  &::before {
    content: '';
    display: inline-block;
    position: relative;
    width: 48px;
    height: 48px;
    bottom: -10px;
    background-image: url('../images/icons/icon-tag.svg');
    background-size: cover; }

  @include pad {
    @include font(20px, 2.1);
    &::before {
      width: 24px;
      height: 24px;
      bottom: -5px; } }
  @include mobile {
    margin-bottom: -5px; } }

.blog_tag {
  @include font(24px, 1.5);
  margin-right: 24px;
  font-weight: 700;
  color: $text-color-primary;

  &:hover {
    color: $color-primary-dark; }

  @include pad {
    @include font(16px, 2.25);
    margin-right: 16px; }
  @include mobile {
    margin-right: 8px; } }


// 文章標籤( 單頁文章 tag )
.article_tags {
  .blog_tag:first-child {
    margin-right: 16px;
    @include pad {
      margin-right: 8px; }
    @include mobile {
      margin-right: 4px; } } }
