// 門市資訊
.location_detail {
  @include pad {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 100%;
    margin-bottom: 32px; }
  @include mobile {
    margin-bottom: 16px; } }

.location_detail_img {
  height: 224px;
  overflow: hidden;

  @include pad {
    max-width: 50%;
    height: 164px;
    padding-right: 15px;
    img {
      transform: translateY(-8%); } }
  @include mobile {
    max-width: 100%;
    height: auto;
    padding: 0;
    img {
      height: 100%;
      transform: translateY(10%) scale(1.5); } } }


// 門市內容
.location_detail_content {
  @include pad {
    width: 100%;
    max-width: 50%;
    padding-left: 15px;
    .location_list_content_title {
      padding: 8px 16px; } }
  @include mobile {
    max-width: 100%;
    padding: 0;
    .location_list_content_title {
      padding-left: 24px;
      padding-right: 24px; } } }

.location_map {
  max-width: 100%;
  width: 60%;
  overflow: hidden;
  iframe {
    @include box-size(100%, 100%); }

  @include pad {
    width: 100%;
    iframe {
      height: 422px; } } }
