.index_title {
  h2 {
    @include font(48px, 1.5);
    margin-bottom: 40px;
    text-align: center;
    @include pad {
      font-size: 32px;
      margin-bottom: 32px; }
    @include mobile {
      @include font(24px, 2);
      margin-bottom: 20px; } } }

.page_padding {
  padding-top: 60px;
  padding-bottom: 80px;

  @include pad {
    padding-top: 40px;
    padding-bottom: 56px; }
  @include mobile {
    padding-top: 32px;
    padding-bottom: 40px; } }


// 表頭
.header {
  background-image: url('../images/banners/banner.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center; }

.header_content {
  height: 840px;
  text-align: right;
  h1 {
    @include font(64px, 1.5); }
  span {
    @include font(36px, 1.5); }

  @include pad {
    h1 {
      font-size: 48px; }
    span {
      font-size: 20px; } }
  @include mobile {
    height: 394px;
    h1 {
      @include font(32px, 2.25); }
    span {
      @include font(16px, 1.875); } } }


// 服務介紹
.service_items {
  background: rgba($color-primary, .05);

  .page_padding {
    @include pad {
      padding-left: 77px;
      padding-right: 77px; }
    @include mobile {
      padding-left: 50px;
      padding-right: 50px; } } }

.service_lists {
  @include pad {
    flex-wrap: wrap; }
  @include mobile {
    justify-content: center; } }

.service_list_item {
  max-width: 24%;

  @include pad {
    max-width: 47%; }
  @include mobile {
    max-width: 100%; } }

.service_list_item:nth-child(1), .service_list_item:nth-child(2) {
  @include pad {
    margin-bottom: 32px; } }

.service_list_item:not(:last-child), {
  @include mobile {
    margin-bottom: 32px; } }

.service_list_icon {
  @include box-size(100px, 100px); }

.service_list_item_title {
  @include font(24px, 1.5);
  padding-top: 16px;
  padding-bottom: 8px; }

.service_list_item_text {
  @include font(16px, 1.5); }


// 產品分類
.product_img {
  max-width: 57%;

  @include pad {
    max-width: 50%; }
  @include mobile {
    max-width: 100%;
    img {
      width: 110%;
      position: relative;
      left: 50%;
      transform: translateX(-50%); } } }

.product_list_item {
  overflow: hidden;

  @include mobile {
    position: relative;
    justify-content: center; } }

.product_list_item:nth-child(2) {
  flex-direction: row-reverse;

  @include pad {
    flex-direction: row; } }

.product_list_item:not(:last-child) {
  margin-bottom: 32px;

  @include pad {
    margin-bottom: 8px; } }

.product_content {
  margin: 0 30px;

  @include mobile {
    position: absolute; } }

.product_icon {
  @include box-size(48px, 48px);
  display: block;
  margin-right: 8px;

  @include pad {
    @include box-size(36px, 36px); } }

.product_text {
  @include font(48px, 1.5);
  text-transform: uppercase;

  @include pad {
    @include font(28px, 2.57); } }


// 聯合產品
.cooperative_products {
  background-image: url('../images/bg.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.cooperative_lists {
  @include mobile {
    flex-wrap: wrap; } }

.cooperative_list_item {
  width: 49%;
  position: relative;
  background: $color-light;

  @include mobile {
    width: 100%; } }

.cooperative_list_item:first-child {
  @include mobile {
    margin-bottom: 16px; } }

.cooperative_img {
  opacity: .5;

  @include mobile {
    width: 110%; } }

.cooperative_text {
  @include font(72px, 1.5);
  position: absolute;
  z-index: 2;

  @include pad {
    @include font(32px, 0.78); } }


// 顧客推薦
.client_recommend_list {
  @include pad {
    flex-wrap: wrap; } }

.client_recommend_card {
  max-width: 23%;
  border: solid 1px $color-gray-100;
  border-radius: 4px;
  box-shadow: 0 2px 6px $color-gray-100;
  overflow: hidden;

  @include pad {
    max-width: 48%; }
  @include mobile {
    max-width: 100%; } }

.client_recommend_card:nth-child(1),
.client_recommend_card:nth-child(2) {
  @include pad {
    margin-bottom: 32px; } }

.client_recommend_card:not(:last-child) {
  @include mobile {
    margin-bottom: 32px; } }

.client_recommend_card_content {
  height: 204px;
  padding: 16px;

  @include pad {
    padding-left: 24px;
    padding-right: 24px; } }

.client_recommend_card_title, .client_recommend_card_date {
  @include font(14px, 1.5); }

.client_recommend_card_title {
  margin-bottom: 8px; }

.client_recommend_card_text {
  @include font(16px, 1.5); }

.client_recommend_card_date {
  color: $color-gray-300; }


// 聯絡表單
.contact_us {
  background: rgba($color-primary, .05);

  .page_padding {
    @include mobile {
      padding-top: 30px; } }

  h2 {
    margin-bottom: 24px;
    @include mobile {
      margin-bottom: 20px; } } }

.contact_us_text {
  @include font(20px, 1.5);
  margin-bottom: 20px;

  @include pad {
    @include font(16px, 1.5);
    margin-bottom: 16px; }
  @include mobile {
    margin-bottom: 8px; } }

.contact_us_content {
  max-width: 60%;
  margin: 0 auto;

  @include mobile {
    max-width: 100%; } }

.contact_form_item {
  margin-bottom: 16px;

  @include pad {
    margin-bottom: 8px; } }

.contact_form_label {
  @include font(16px, 1.5);
  margin-bottom: 4px;

  @include pad {
    @include font(14px, 1.5); } }

.contact_form_input {
  @include font(16px, 1.5);
  width: 100%;
  padding: 12px 16px;
  border: solid 1px $color-dark;
  background: transparent; }

.contact_form_checkbox {
  margin-bottom: 40px;
  input {
    @include box-size(15px, 15px); }
  label {
    display: block; }

  @include pad {
    margin-bottom: 20px; } }

.contact_form_buttons {
  text-align: center; }

.contact_form_submit_button {
  width: 50%;
  border: none;
  padding: 16px;
  font-weight: 700;
  color: $color-light;
  background: $color-primary;
  transition-duration: .3s;

  &:hover {
    background: darken($color-primary, 5);
    cursor: pointer;
    transition-duration: .3s; } }
