// 分類主區塊
.pagination {
  display: none;
  margin-top: 96px;

  @include pad {
    margin-top: 65px; }
  @include mobile {
    display: block;
    margin-top: 40px; } }

.pagination_form {
  max-width: 50%;
  margin: 0 auto;

  @include mobile {
    max-width: 100%; } }

.pagination_label {
  border: solid 1px $color-gray-500;
  display: block;
  &:hover {
    color: $color-light;
    background: $color-dark;
    cursor: pointer; } }


// li 容器
.pagination_item {
  @include pad {
    flex: 2 0 auto;
    text-align: center; } }


// 連結
.pagination_link {
  display: none;
  &:checked + .pagination_label {
    color: $color-light;
    background: $color-dark; } }

// 連結數字
.pagination_link_num {
  @include font(18px, 1.5);
  padding: 9.5px 16px;

  @include pad {
    padding-left: 0;
    padding-right: 0; }
  @include mobile {
    font-size: 16px; } }

// 連結文字
.pagination_link_text {
  @include font(19px, 1.53);
  padding: 8.5px 12px;

  @include pad {
    padding-left: 0;
    padding-right: 0; }
  @include mobile {
    font-size: 17px; } }

.pagination_item:last-child {
  background: $color-gray-100; }
